import React, { Component } from 'react'
import { Tabs, Panel, TabsItem, PanelHeader, Div, Button, FixedLayout} from '@vkontakte/vkui';

import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';
import Icon28ViewOutline from '@vkontakte/icons/dist/28/view_outline';

export default class Aviasales extends Component {
    state = {
        activeTab:'aviasales',
    }

    render() {
        const { id } = this.props;
        const { activeTab } = this.state;
        return (
            <Panel id={id}>
                <PanelHeader separator={false}>Авиабилеты</PanelHeader>
                <Tabs>
                    <TabsItem selected={activeTab === 'aviasales'} onClick={() => this.setState({ activeTab: 'aviasales'})}>Aviasales</TabsItem>
                    <TabsItem selected={activeTab === 'skyscanner'} onClick={() => this.setState({ activeTab: 'skyscanner'})}>Skyscanner</TabsItem>
                </Tabs>

                <FixedLayout vertical='bottom'>
                    <Div align="center" style={{ paddingBottom:6 }}>
                        <Button className='circle_button' onClick={() => {
                                                            if(this.state.is_open) this.openMenu()
                                                            else this.openMenuPlaceholder()
                        }}><Icon28ViewOutline />
                        </Button>
                    </Div>
                </FixedLayout>
            </Panel>
        )
    }
}